<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <!-- to -->
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!---->
     <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

      </el-row>


      <!--  -->
      <!-- :data border stripe-->
      <el-table :data="fileList" border stripe>
        <!-- label prop -->
        <!-- type='index'  -->
        <el-table-column label="id" prop="id"></el-table-column>
        <el-table-column label="" prop="filename" style="color:#409EFF;" ></el-table-column>
        <el-table-column label="" prop="founder"></el-table-column>
        <el-table-column label="" prop="creationdata"></el-table-column>
        <el-table-column label="" prop="filesize"></el-table-column>
        <el-table-column label="" width="180">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip content="" placement="top">
            <el-link type="primary" :href="'http://127.0.0.1:8888/OA_System/download?fileName='+scope.row.filename"></el-link>            
        </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 
      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

  


  </div>
</template>

<script>
export default {


 created(){
    this.getFileList()
  },

  
  data() {
 
    return {
      queryInfo: {
        // 
        query:'',
        pagesize: 2,
        // 
        pagenum: 1,
        // 
        total: 0
      },

      editForm:{

      },
      fileList:[
     
      ],

    }
  },
 
  methods: {
     // pagesize 
     handleSizeChange(newSize) {
       console.log(newSize)
       this.queryInfo.pagesize = newSize
       this.getFileList()
     },
      //
     handleCurrentChange(newPage) {
       console.log(newPage)
       this.queryInfo.pagenum = newPage
       this.getFileList()
     },

    removeFileById(id){},


    async getFileList(){
      const{data:res} = await this.$http.get('fileList',{
        params:this.queryInfo
      })
      if(res.meta.status != 200) {
        return this.$message.error('')
      }
      this.fileList = res.data.files
      this.total = res.data.total
      console.log(res.data)

    }

 
    }

}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-pagination {
  margin-top: 15px;
}
html,
body,
#app {
  height: 100%;
  margin: 0;
  padding: 0;
}

.el-breadcrumb {
  margin-bottom: 15px;
  font-size: 12px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
</style>



