<template>
    <div>
      <!---->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

      <!---->
      <!---->
      <el-card class="box-card">

        <el-row :gutter="20">
          <el-col :span="8">
            <el-input placeholder="">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible = true"></el-button>
          </el-col>
        </el-row>

        <!---->
        <el-table :data="userList" border stripe>
          <el-table-column label="id" prop="id" width="50px"></el-table-column>
          <el-table-column label="" prop="username"></el-table-column>
          <el-table-column label="" prop="name"></el-table-column>
          <el-table-column label="" prop="tel"></el-table-column>
          <el-table-column label="" prop="role.name"></el-table-column>
          <el-table-column label="" prop="post.name"></el-table-column>
          <el-table-column label="" prop="depart.name"></el-table-column>


          <!----------------------------------------------------------------------->
          <el-table-column label="" width="180px">
            <template slot-scope="scope">
              <!---->
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id,scope.name)"></el-button>
              </el-tooltip>
              <!---->
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete"
              @click="removeUserById(scope.row.id)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>


        </el-table>


        <!---------------------------------------------------------------------------->
        <span class="demonstration"></span>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 2, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>

      </el-card>

      <!----------------------------------------------------------------------->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed">
        <!---->
        <span>
          <el-form :model="addForm" :rules="addFormRules"
                   ref="addFormRef" label-width="70px" >
    <el-form-item label="" prop="username">
      <el-input v-model="addForm.username"></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
      <el-input v-model="addForm.password"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="addForm.name"></el-input>
    </el-form-item>
    <el-form-item label="" prop="tel">
      <el-input v-model="addForm.tel"></el-input>
    </el-form-item>
          </el-form>


 <div style="margin-left: 27px;margin-top: 10px;">
   
              <el-radio-group v-model="addForm.role.id" style="margin-left:5px; margin-right:15px">
      <el-radio-button label=1></el-radio-button>
      <el-radio-button label=2></el-radio-button>
      <el-radio-button label=3></el-radio-button>

    </el-radio-group>
 </div>

    <div style="margin-left: 27px;margin-top: 25px;">
      

          <el-select v-model="addForm.post.id" placeholder="">
    <el-option
      v-for="item in postList"
      :key="item.id"
      :label="item.name"
      :value="item.id">  <!---->
    </el-option>
  </el-select>

           <el-select v-model="addForm.depart.id" placeholder="">
    <el-option
      v-for="item in departList"
      :key="item.id"
      :label="item.name"
      :value="item.id">  <!---->
    </el-option>
  </el-select>
    </div>




        </span>
        <!---->
        <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addUser"> </el-button>
  </span>
      </el-dialog>







      <!---->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%">
        <!---->
        <span>
          <el-form :model="editForm" :rules="addFormRules"
                   ref="editFormRef" label-width="70px" >
    <el-form-item label="" prop="username">
      <el-input v-model="editForm.username"  disabled="false"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="editForm.name"></el-input>
    </el-form-item>
    <el-form-item label="" prop="tel">
      <el-input v-model="editForm.tel"></el-input>
    </el-form-item>
          </el-form>


 <div style="margin-left: 27px;margin-top: 10px;">
   
              <el-radio-group v-model="editForm.role.id" style="margin-left:5px; margin-right:15px">
      <el-radio-button label=1></el-radio-button>
      <el-radio-button label=2></el-radio-button>
      <el-radio-button label=3></el-radio-button>

    </el-radio-group>
 </div>


    <div style="margin-left: 27px;margin-top: 25px;">
      

          <el-select v-model="editForm.post.id" placeholder="">
    <el-option
      v-for="item in postList"
      :key="item.id"
      :label="item.name"
      :value="item.id">
    </el-option>
  </el-select>

           <el-select v-model="editForm.depart.id" placeholder="">
    <el-option
      v-for="item in departList"
      :key="item.id"
      :label="item.name"
      :value="item.id">
    </el-option>
  </el-select>
    </div>





        </span>
        <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUser"> </el-button>
  </span>
      </el-dialog>




    </div>
</template>

<script>
    export default {
        name: "users",
      data(){
          return {
            //
            queryInfo:{
              query:'',
              pagenum:1,
              pagesize:2
            },
            userList:[],
            postList:[],
            departList:[],

            total:0,
            //
            addDialogVisible:false,
            editDialogVisible:false,
            //
            addForm:{
              /*id:0,*/
              username:'',
              password:'',
              name:'',
              tel:'',
              role:{id:1},
              post:{id:1},
              depart:{id:1},
            },
            //
            addFormRules:{
              username:[{
                required: true,message: '',trigger:
        'blur'},
                { min:3,
                  max:10,
                  message:'3-10',
                  trigger:'blur'

                }

                  ],

              password:[{
                required: true,message: '',trigger:
                  'blur'},
                { min:6,
                  max:15,
                  message:'6-15',
                  trigger:'blur'

                }

              ],
            },
            //
            editForm:{
              role:{id:1},
              post:{id:2},
              depart:{id:1}
            }






          }
      },
      created(){
          this.getUserList(),
          this.getPostList(),
            this.getDepartList()
      },
      methods:{
          async getUserList(){
           const{data:res} = await this.$http.get('users',{
             params:this.queryInfo
           })
            if(res.meta.status != 200) {
              return this.$message.error('')
            }
            this.userList = res.data.users
            this.total = res.data.total
            console.log(res)
          },
      //pagesize
        handleSizeChange(newSize){
            console.log(newSize)
            this.queryInfo.pagesize = newSize
            this.getUserList()
        },
        //
        handleCurrentChange(newPage){
            console.log(newPage)
          this.queryInfo.pagenum = newPage
          this.getUserList()

        },

        async getPostList(){
          const{data:res} = await this.$http.get('posts',
          )
          if(res.meta.status != 200) {
            return this.$message.error('')
          }
          this.postList = res.data

        },

        async getDepartList(){
          const{data:res} = await this.$http.get('departs',
          )
          if(res.meta.status != 200) {
            return this.$message.error('')
          }
          this.departList = res.data

        },
        //
        addDialogClosed(){
            this.$refs.addFormRef.resetFields()
        },
        //
        addUser(){
          this.$refs.addFormRef.validate(async valid => {
            if(!valid) return
            //
            const {data:res} = await this.$http.post('users',this.addForm)
            if(res.meta.status !== 200) {
              this.$message.error('!')
            }
            this.$message.success('!')
            //
            this.addDialogVisible = false
            //
            this.getUserList()
          })
        },


        async showEditDialog(id,name){
          console.log(name)

          /*console.log(this.editId)*/
          const {data: res} =  await this.$http.get('users/'+id)
          if(res.meta.status != 200){
            return this.$message.error('!')
          }
          this.editForm = res.data
          this.editDialogVisible = true
        },

        async editUser(){
            const {data:res} = await this.$http.put('users',this.editForm)
            if(res.meta.status !== 200) {
              this.$message.error('!')
            }
            this.$message.success('!')
            //
            this.editDialogVisible = false
            //
            this.getUserList()
          },

        //Id
        async removeUserById(id){
            console.log(id)
          //
          const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })/*.catch(error => {
            return err
          })*/
            .catch(err => err)
          //confirm
          //,cancel
          /*console.log(confirmResult)*/
          if(confirmResult !== 'confirm'){
            return this.$message.info('')
          }
          /*console.log('')*/
          const {data:res} = await this.$http.delete('users/'+id)

          if(res.meta.status!==200){
            return this.$message.error('!')
          }
          this.$message.success('!')
          this.getUserList()
        }


      }
    }

</script>

<style scoped>

</style>



