<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <!-- table -->
    <el-table :data="VoteForm" border stripe>
      <el-table-column type="index" label=""></el-table-column>
      <el-table-column prop="founder" label=""></el-table-column>
      <el-table-column prop="creationdata" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <!--  -->
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" @click="context(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog title="" :visible.sync="ActivityContentDialogVisible" width="50%">
      <!--  -->
      <el-row>{{this.Content}}</el-row>
      <br />
      <el-row :gutter="20">
        <el-col :span="10">*</el-col>
        <el-col :span="8">
          <el-button type="success" icon="el-icon-check" circle @click="Vote = 1"></el-button>
          <el-button type="danger" icon="el-icon-close" circle @click="Vote = 2"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="ActivityContentDialogVisible = false"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created(){
    this.getVoteActList()
  },
  data() {
    return {
      //   VoteForm
      VoteForm: [
        {
          name: 'dfdf',
          time: '2012-12-12',
          title: '',
          content: ''
        }
       
      ],
      //     
      // 
      Vote: 0,
      //  false
      ActivityContentDialogVisible: false,
      //   
      Content: '',
      nowId:''
    }
  },
  methods: {
    // 
    context(content) {
      this.nowId = content.id
      //  true
      this.ActivityContentDialogVisible = true
      // contentContent Content
      this.Content = content.content
    },
    //   
    async submit() {
      //console.log(this.nowId)
      if (this.Vote === 0) {
        // 
        return this.$message.error('')
      }
      // this.vote  1 2
      const{data:res} = await this.$http.post('vote/'+this.nowId+'/'+this.Vote+'/'+sessionStorage.getItem('id'))
      // 
      if(res.meta.status != 200)
      this.$message.error('')
      else
      this.$message.success('')
      // Vote
      this.Vote = 0
      // 
      this.ActivityContentDialogVisible = false
    },

    async getVoteActList(){
        const{data:res} = await this.$http.get('voteListToVote')
         if(res.meta.status != 200) {
        return this.$message.error('')
      }
      this.VoteForm = res.data
    }
  }
  }

</script>
<style lang="less" scoped>
html,
body,
#app {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 1366;
}

.el-breadcrumb {
  margin-bottom: 15px;
  font-size: 12px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
</style>



