<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-form
        :rules="announcementFormRules"
        ref="announcementFormRef"
        :model="announcementForm"
        label-width="80px"
        class="demo-ruleForm"
      >
        <!--  -->
        <el-form-item label="" prop="title">
          <el-input v-model="announcementForm.title" style="width:150px"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="content">
          <el-input
            v-model="announcementForm.content"
            type="textarea"
            :rows="10"
            style="width:450px"
            placeholder=""
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item>
          <el-button type="primary" @click="reset('announcementFormRef')"></el-button>
          <el-button type="danger" @click="submit('announcementFormRef')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      announcementForm: {
        // 
        title: '',
        // 
        content: ''
      },
      // 
      announcementFormRules: {
        title: [{ required: true, message: '!', trigger: 'blur' }],
        content: [{ required: true, message: '!', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    reset(announcementFormRef) {
      this.$refs[announcementFormRef].resetFields()
    },
    // 
    submit(announcementFormRef) {
      // 
      this.$refs[announcementFormRef].validate(async valid => {
        if (valid) {
          const{data:res} = await this.$http.post('anno/'+sessionStorage.getItem('id'),this.announcementForm)
          if(res.meta.status != 200)
          return this.$message.error('')
          return this.$message.success('')
        } else {
          // 
          // 
          return this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
</style>



