<template>
  <div>
    <!-- table -->
    <el-table :data="CommonForm" border stripe>
      <el-table-column type="index" label=""></el-table-column>
      <el-table-column prop="founder" label=""></el-table-column>
      <el-table-column prop="creationdata" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <!--  -->
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" @click="context(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog title="" :visible.sync="ActivityContentDialogVisible" width="50%">
      <span>{{this.Content}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="ActivityContentDialogVisible = false"> </el-button>
        <el-button type="primary" @click="ActivityContentDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created(){
     this.getCommonList()
  },
  data() {
    return {
      //    CommonForm
      CommonForm: [
        {
          founder: 'dfdf',
          creationdata: '2012-12-12',
          title: '',
          content: ''
        },
        
      ],
      //   
      //  false
      ActivityContentDialogVisible: false,
      //   
      Content: ''
    }
  },
  methods: {
    context(content) {
      this.ActivityContentDialogVisible = true
      // Content
      this.Content = content.content
    },

    async getCommonList(){
      const{data:res} = await this.$http.get('CommonList')
      this.CommonForm = res.data
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
</style>



