<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <!-- to -->
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!---->
     <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="">
            <el-button  slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <!-- :data border stripe-->
      <el-table :data="departList" border stripe>
        <!-- label prop -->
        <!-- type='index'  -->
        <el-table-column label="id" prop="id"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="manager"></el-table-column>
        <el-table-column label="" prop="tel"></el-table-column>
        <el-table-column label="" width="180">
          <!--  -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip content="" placement="top">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeDepartById(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 
      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <!--
      :visible.sync="dialogVisible"   datadialogVisible = false
       dialogVisible = true
    -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!--  -->
      <!--
        :model="addDepartment"  v-model
        :rules="addDepartmentRules"  propprop
        ref="addDepartmentRef"   @close="addDialogClosed"
        prop
      -->
      <el-form :model="addDepartment" :rules="addDepartmentRules" ref="addDepartmentRef">
        <el-form-item label="" prop="name">
          <el-input v-model="addDepartment.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="manager">
          <el-input v-model="addDepartment.manager"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="addDepartment.tel"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addDepart"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%">
      <!---->
      <span>
          <el-form :model="editForm" :rules="addDepartmentRules"
                   ref="editFormRef" label-width="70px" >
    <el-form-item label="" prop="username">
      <el-input v-model="editForm.name" ></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="editForm.manager"></el-input>
    </el-form-item>
    <el-form-item label="" prop="tel">
      <el-input v-model="editForm.tel"></el-input>
    </el-form-item>
          </el-form>


        </span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUser"> </el-button>
  </span>
    </el-dialog>

  </div>
</template>

<script>
export default {


 created(){
      this.getDepartList()
  },

  
  data() {
    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^[1][3,4,5,7,8][0-9]{9}$/
      if (regMobile.test(value)) {
        // 
        return cb()
      }
      // 
      cb(new Error(''))
    }
    return {
      queryInfo: {
        // 
        query:'',
        pagesize: 2,
        // 
        pagenum: 1,
        // 
        total: 0
      },
      // 
      addDialogVisible: false,
      // 
      addDepartment: {
        name: '',
        manager: '',
        tel: ''
      },
      editForm:{

      },
      departList:[],
      // 
      addDepartmentRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        manager: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tel: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      editDialogVisible:false

    }
  },
 
  methods: {
    //  pagesize 
    handleSizeChange(newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getDepartList()
    },
    // 
    handleCurrentChange(newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getDepartList()
    },

    addDialogClosed() {
      // addDepartmentRef  
      this.$refs.addDepartmentRef.resetFields()
    },
    async getDepartList(){
      const{data:res} = await this.$http.get('departsByPage',{
        params:this.queryInfo
      })
      if(res.meta.status != 200) {
        return this.$message.error('')
      }
      this.departList = res.data.departs
      this.total = res.data.total
      console.log(res)
    },
    addDepart(){
      this.$refs.addDepartmentRef.validate(async valid => {
        if(!valid) return
        //
        const {data:res} = await this.$http.post('departs',this.addDepartment)
        if(res.meta.status !== 200) {
          this.$message.error('!')
        }
        this.$message.success('!')
        //
        this.getDepartList()
        this.addDialogVisible = false
        //
      })
    },
    async showEditDialog(id){
      const{data:res} = await this.$http.get('departs/'+id)
      if(res.meta.status != 200){
        return this.$message.error('!')
      }
      this.editForm = res.data
      this.editDialogVisible = true

    },


    editUser(){
        this.$refs.editFormRef.validate(async valid =>{
          if(!valid) return
          const {data:res} = await this.$http.put('departs',this.editForm)
          if(res.meta.status !=200 ){
            this.$message.error('!')
          }
          this.$message.success('!')
          //
          this.editDialogVisible = false
          this.getDepartList()

        })

      },
    async removeDepartById(id){
      const confirmResult = await this.$confirm(', ?',{
        confirmButtonText: '',
        cancelButtonText: '',
        type:'warning'
      })
        .catch(err => err)
      if(confirmResult !== 'confirm'){
        return this.$message.info('')
      }
      /*console.log('')*/
      const {data:res} = await this.$http.delete('departs/'+id)

      if(res.meta.status!==200){
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getDepartList()
    }
    }

}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-pagination {
  margin-top: 15px;
}
html,
body,
#app {
  height: 100%;
  margin: 0;
  padding: 0;
}

.el-breadcrumb {
  margin-bottom: 15px;
  font-size: 12px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
</style>



