<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-form
        :model="PasswordForm"
        :rules="PasswordFormRules"
        ref="PasswordFormRef"
        label-width="150px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="oldPassword">
          <el-input v-model="PasswordForm.oldPassword"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input v-model="PasswordForm.newPassword"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rePassword">
          <el-input v-model="PasswordForm.rePassword"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item>
          <el-button type="primary" @click="changePassword('PasswordFormRef')"></el-button>
          <el-button @click="resetForm('PasswordFormRef')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.PasswordForm.newPassword) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      // 
      PasswordForm: {
        // 
        oldPassword: '',
        // 
        newPassword: '',
        // 
        rePassword: ''
      },
      // 
      PasswordFormRules: {
        newPassword: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        oldPassword: [
          { required: true, message: '!', trigger: 'blur' }
        ],
        rePassword: [{ validator: validatePass2, trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    resetForm(PasswordFormRef) {
      this.$refs[PasswordFormRef].resetFields()
    },
    // 
    changePassword(PasswordFormRef) {
      // 
      this.$refs[PasswordFormRef].validate(async valid => {
        if (valid) {
          // 
          console.log(this.PasswordForm)
          // 
          const{data:res} = await this.$http.put('password/'+sessionStorage.getItem('id'),{oldPassword:this.PasswordForm.oldPassword,newPassword:this.PasswordForm.newPassword} )
          // 
          if(res.meta.status != 200){
          return this.$message.error('')
          }
          this.$message.success('')
        } else {
          // 
          console.log('error submit!!')
          // 
          return this.$message.error('')
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-input {
  padding: 15px;
  width: 300px;
}
html,
body,
#app{
    height: 100%;
    margin: 0;
    padding: 0;
    min-width: 1366;
}

.el-breadcrumb{
    margin-bottom: 15px;
    font-size: 12px;
}
.el-card{
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important
}
</style>



