<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-form
        :model="addActivityForm"
        :rules="addActivityFormRules"
        ref="addActivityFormRef"
        label-width="100px"
      >
        <!--  -->
        <el-form-item label="" prop="title">
          <el-input v-model="addActivityForm.title" style="width:150px"></el-input>
        </el-form-item>
        <!--  label  -->
        <el-form-item label="" prop="type">
          <template>
            <el-radio v-model="addActivityForm.type" label="1"></el-radio>
            <el-radio v-model="addActivityForm.type" label="2"></el-radio>
          </template>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="content">
          <el-input v-model="addActivityForm.content" type="textarea" :rows="10"></el-input>
        </el-form-item>
        <!--  -->
        <el-row :gutter="20">
          <el-col :span="9">.</el-col>
         
            <el-button type="danger" @click="resetForm('addActivityFormRef')"></el-button>
            <el-button type="primary" @click="submit('addActivityFormRef')"></el-button>
         
        </el-row>
      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      addActivityForm: {
        // 
        title: '',
        // 
        type: '',
        // 
        content: ''
      },
      // 
      addActivityFormRules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        type: [{ required: true, message: '', trigger: 'blur' }],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    // 
    submit(formName) {
      // 
      this.$refs[formName].validate(async valid => {
        if (valid) {
          // 
          // 
          const {data:res} = await this.$http.post('act/'+sessionStorage.getItem('id'),this.addActivityForm)
          if(res.meta.status == 200)
          return this.$message.success('')
        } else {
          // 
          console.log('error submit!!')
          // 
          return this.$message.error('')
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
html,
body,
#app {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 1366;
}

.el-breadcrumb {
  margin-bottom: 15px;
  font-size: 12px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
</style>



