<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary" @click="Connom"></el-button>
        </el-col>
        <el-col :span="4">
          <el-button type="danger" @click="Vote"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <router-view></router-view>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  methods: {
    // 
    Connom() {
      // 
      this.$router.push('/ViewActivityCommon')
    },
    // 
    Vote() {
      // 
      this.$router.push('/ViewActivityVote')
    }
  }
}
</script>

<style lang="less" scoped>
</style>



