<template>
  <div>
    <!-- table -->
    <el-table :data="VoteForm" border stripe>
      <el-table-column type="index" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="time" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <!--  -->
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" @click="context(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog title="" :visible.sync="ActivityContentDialogVisible" width="50%">
      <!--  -->
      <el-row>{{this.Content}}</el-row>
      <br />
      <!--  -->
      <!--  -->
      <el-row>
        <el-col :span="5"></el-col>
        <el-col :span="8">
          <el-progress :percentage="AgreeP" :text-inside="true" :stroke-width="24"  status="success"></el-progress>
        </el-col>
        <el-col :span="8">{{this.Agree}}</el-col>
      </el-row>
      <!--  -->
      <el-row>
        <el-col :span="5"></el-col>
        <el-col :span="8">
          <el-progress
            :percentage="DisAgreeP"
            :text-inside="true"
            :stroke-width="24"
            status="exception"
          ></el-progress>
        </el-col>
        <el-col :span="8">{{this.DisAgree}}</el-col>

      </el-row>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="contextReset"> </el-button>
        <el-button type="primary" @click="contextReset"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created(){
    this.getVoteActList()
  },
  data() {
    return {
      //   VoteForm
      VoteForm: [
        {
          name: 'dfdf',
          time: '2012-12-12',
          title: '',
          content: '',
          Agree: '30.55',
          DisAgree: '69.55'
        },
        
      ],
      //     
      //  false
      ActivityContentDialogVisible: false,
      //   
      Content: '',
      // 
      Agree: '',
      // 
      DisAgree: '',
      //
      AgreeP:0,
      //
      DisAgreeP:0

    }
  },
  methods: {
    // 
    context(content) {
      //  true
      this.ActivityContentDialogVisible = true
      // contentContent Content 
      this.Content = content.content
      // AgreeAgree  Agree
      this.Agree = content.Agree
      this.DisAgree = content.DisAgree
    if(this.Agree+this.DisAgree != 0){
      this.AgreeP = (this.Agree/(this.Agree+this.DisAgree))*100
      // DisAgreeDisAgree  DisAgree
      this.DisAgreeP =  (this.DisAgree/(this.Agree+this.DisAgree))*100
    }
      
      
    },
    contextReset(){
      this.ActivityContentDialogVisible = false
      this.contextReset1()

    },
    contextReset1(){
      this.AgreeP=0
      //
      this.DisAgreeP=0
    },
  


    async getVoteActList(){
        const{data:res} = await this.$http.get('voteList')
         if(res.meta.status != 200) {
        return this.$message.error('')
      }
      this.VoteForm = res.data
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
</style>



